import { useState } from 'react';
import {
  Box,
  Paper,
  Typography,
  Button,
  TextField,
  Grid,
  Card,
  CardContent,
  Chip,
  IconButton,
  Divider,
  List,
  ListItem,
  ListItemText,
  Alert,
} from '@mui/material';
import {
  Preview as PreviewIcon,
  Edit as EditIcon,
  Undo as UndoIcon,
  Redo as RedoIcon,
  Save as SaveIcon,
  Refresh as RefreshIcon,
  Compare as CompareIcon,
} from '@mui/icons-material';
import { motion } from 'framer-motion';

interface PreviewData {
  id: string;
  version: number;
  html: string;
  css: string;
  changes: Array<{
    type: string;
    target: string;
    property: string;
    oldValue: any;
    newValue: any;
  }>;
  confidence: number;
}

const PreviewEditor: React.FC = () => {
  const [currentPreview, setCurrentPreview] = useState<PreviewData | null>(null);
  const [refinementInput, setRefinementInput] = useState('');
  const [isEditing, setIsEditing] = useState(false);
  const [history, setHistory] = useState<PreviewData[]>([]);
  const [historyIndex, setHistoryIndex] = useState(-1);

  // 
  const generateMockPreview = (): PreviewData => ({
    id: `preview_${Date.now()}`,
    version: history.length + 1,
    html: `
      <div class="preview-container">
        <h1 class="title"></h1>
        <p class="description"></p>
        <div class="price">1,000</div>
        <button class="action-button"></button>
      </div>
    `,
    css: `
      .preview-container {
        padding: 20px;
        font-family: sans-serif;
      }
      .title {
        font-size: 24px;
        color: #333;
        margin-bottom: 10px;
      }
      .description {
        font-size: 14px;
        color: #666;
        margin-bottom: 15px;
      }
      .price {
        font-size: 20px;
        font-weight: bold;
        color: #e74c3c;
        margin-bottom: 20px;
      }
      .action-button {
        background: #3498db;
        color: white;
        border: none;
        padding: 10px 20px;
        border-radius: 4px;
        cursor: pointer;
      }
    `,
    changes: [
      {
        type: 'style',
        target: '.title',
        property: 'font-size',
        oldValue: '16px',
        newValue: '24px',
      },
      {
        type: 'content',
        target: '.price',
        property: 'text',
        oldValue: '2,000',
        newValue: '1,000',
      },
    ],
    confidence: 0.85,
  });

  const handleGeneratePreview = () => {
    const newPreview = generateMockPreview();
    setCurrentPreview(newPreview);
    setHistory([...history.slice(0, historyIndex + 1), newPreview]);
    setHistoryIndex(historyIndex + 1);
  };

  const handleRefine = () => {
    if (!currentPreview || !refinementInput) return;

    // 
    const refinedPreview = { ...currentPreview };
    
    // 
    const priceMatch = refinementInput.match(/(\d+)/);
    if (priceMatch) {
      refinedPreview.html = refinedPreview.html.replace(
        /<div class="price">.*?<\/div>/,
        `<div class="price">${priceMatch[1]}</div>`
      );
      refinedPreview.changes.push({
        type: 'content',
        target: '.price',
        property: 'text',
        oldValue: currentPreview.html.match(/<div class="price">(.*?)<\/div>/)?.[1],
        newValue: `${priceMatch[1]}`,
      });
    }

    // 
    if (refinementInput.includes('')) {
      refinedPreview.css = refinedPreview.css.replace(
        /font-size: 24px/,
        'font-size: 32px'
      );
      refinedPreview.changes.push({
        type: 'style',
        target: '.title',
        property: 'font-size',
        oldValue: '24px',
        newValue: '32px',
      });
    }

    refinedPreview.version = history.length + 1;
    refinedPreview.id = `preview_${Date.now()}`;

    setCurrentPreview(refinedPreview);
    setHistory([...history.slice(0, historyIndex + 1), refinedPreview]);
    setHistoryIndex(historyIndex + 1);
    setRefinementInput('');
  };

  const handleUndo = () => {
    if (historyIndex > 0) {
      setHistoryIndex(historyIndex - 1);
      setCurrentPreview(history[historyIndex - 1]);
    }
  };

  const handleRedo = () => {
    if (historyIndex < history.length - 1) {
      setHistoryIndex(historyIndex + 1);
      setCurrentPreview(history[historyIndex + 1]);
    }
  };

  const handleApply = () => {
    // API
    alert('');
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        
      </Typography>

      {/*  */}
      <Paper sx={{ p: 2, mb: 3 }}>
        <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
          <Button
            variant="contained"
            startIcon={<PreviewIcon />}
            onClick={handleGeneratePreview}
          >
            
          </Button>
          
          <IconButton
            onClick={handleUndo}
            disabled={historyIndex <= 0}
            title=""
          >
            <UndoIcon />
          </IconButton>
          
          <IconButton
            onClick={handleRedo}
            disabled={historyIndex >= history.length - 1}
            title=""
          >
            <RedoIcon />
          </IconButton>
          
          <Box sx={{ flexGrow: 1 }} />
          
          {currentPreview && (
            <>
              <Chip
                label={`v${currentPreview.version}`}
                color="primary"
                size="small"
              />
              <Chip
                label={`: ${(currentPreview.confidence * 100).toFixed(0)}%`}
                color={currentPreview.confidence > 0.7 ? 'success' : 'warning'}
                size="small"
              />
              <Button
                variant="outlined"
                startIcon={<SaveIcon />}
                onClick={handleApply}
                color="success"
              >
                
              </Button>
            </>
          )}
        </Box>
      </Paper>

      {currentPreview ? (
        <Grid container spacing={3}>
          {/*  */}
          <Grid item xs={12} md={6}>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  
                </Typography>
                <Paper
                  sx={{
                    p: 2,
                    bgcolor: 'background.default',
                    minHeight: 400,
                    position: 'relative',
                  }}
                >
                  <style dangerouslySetInnerHTML={{ __html: currentPreview.css }} />
                  <div dangerouslySetInnerHTML={{ __html: currentPreview.html }} />
                </Paper>
              </CardContent>
            </Card>
          </Grid>

          {/*  */}
          <Grid item xs={12} md={6}>
            <Card sx={{ mb: 3 }}>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  
                </Typography>
                <TextField
                  fullWidth
                  multiline
                  rows={3}
                  variant="outlined"
                  placeholder="500"
                  value={refinementInput}
                  onChange={(e) => setRefinementInput(e.target.value)}
                  sx={{ mb: 2 }}
                />
                <Button
                  variant="contained"
                  startIcon={<RefreshIcon />}
                  onClick={handleRefine}
                  disabled={!refinementInput}
                  fullWidth
                >
                  
                </Button>
              </CardContent>
            </Card>

            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  
                </Typography>
                <List dense>
                  {currentPreview.changes.map((change, index) => (
                    <ListItem key={index}>
                      <ListItemText
                        primary={`${change.target}  ${change.property}`}
                        secondary={`${change.oldValue}  ${change.newValue}`}
                      />
                    </ListItem>
                  ))}
                </List>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      ) : (
        <Paper sx={{ p: 4, textAlign: 'center' }}>
          <PreviewIcon sx={{ fontSize: 64, color: 'text.secondary', mb: 2 }} />
          <Typography variant="h6" color="text.secondary">
            
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
            
          </Typography>
        </Paper>
      )}

      {/*  */}
      <Alert severity="info" sx={{ mt: 3 }}>
        <Typography variant="subtitle2" gutterBottom>
          
        </Typography>
        <Typography variant="body2">
           
        </Typography>
        <Typography variant="body2">
           
        </Typography>
        <Typography variant="body2">
           
        </Typography>
      </Alert>
    </Box>
  );
};

export default PreviewEditor;